'use strict';

/**
 * A component to greet users
 */

// jscs:disable
class Hello extends HTMLElement {

  /*
   * Initial function called when the component is created
   */
  createdCallback() {

    const shadow = this.createShadowRoot(),
      template = createTemplate(getTemplateHtml(), getTemplateStyle()),
      clone = document.importNode(template.content, true);
    shadow.appendChild(clone);

  }

  /*
   * Function called when the component is attached to the DOM
   */
  attachedCallback() {}

  /*
   * Function called when the component is detached from the DOM
   */
  detachedCallback() {}

  /*
   * Function called when some attribute from the component is changed
   */
  attributeChangedCallback(attrName, oldValue, newValue) {}
}

/*************************Helper Functions**********************/

//TODO Aux functions that later will be put in other class
document.registerElement('pw-hello', Hello);
export default Hello;

function createTemplate(tHtml, tStyle) {

  const template = document.createElement('template') ,
    div = document.createElement('div');

  div.innerHTML = tStyle + tHtml;
  while (div.childNodes.length > 0) {

    template.content.appendChild(div.childNodes[0]);

  }

  return template;

}

function getTemplateHtml() {

  return`<div class="outer">
            <div class="boilerplate">
              Hi! My name is
            </div>
            <div class="name">
              <content></content>
            </div>
          </div>`;

}

function getTemplateStyle() {

  return`<style>
            .outer {
              border: 2px solid brown;
              border-radius: 1em;
              background: red;
              font-size: 20pt;
              width: 12em;
              height: 7em;
              text-align: center;
            }
            .boilerplate {
              color: white;
              font-family: sans-serif;
              padding: 0.5em;
            }
            .name {
              color: black;
              background: white;
              font-family: "Marker Felt", cursive;
              font-size: 45pt;
              padding-top: 0.2em;
            }
        </style>`;

}




